<template>
  <div id="app">
    <img src="./assets/logo.gif">
    <span v-html="html"></span>
    <input type="text" v-model="html">
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      html: 'teste'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



